* {
    box-sizing: border-box;
    margin: 0;
}

.body {
    font-family: 'Montserrat' , 'Arial', sans-serif;
    font-style: normal;
    background: #FFFFFF;
}

.container {
    max-width: 1350px;
    min-width: 375px;
    margin: 0 auto;
    padding: 0 30px;
}

.img-responsive {
    max-width: 100%;
    height: auto;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }

.a {
    display: block;
  }

